.empty-container {
  border: 2px dashed rgba(196, 196, 196, 1);
  width: 250px;
  display: flex;
  flex-direction: column;
  gap: 8px;
  justify-content: center;
  align-items: center;
  height: 450px;
  border-radius: 5px;
}

.filled-container {
  height: 450px;
  width: 245px;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.empty-container__info {
  width: 60%;
  text-align: center;
  pointer-events: none;
}

.empty-container__info__text {
  margin-top: 16px;
}

.empty-container__info__text span {
  font-size: 14px;
  font-weight: 500;
  line-height: 17px;
  letter-spacing: 0em;
  text-align: center;
  color: var(--purple-color);
}

.empty-container__info__text p {
  font-size: 12px;
  font-weight: 400;
  line-height: 15px;
  letter-spacing: 0em;
  text-align: center;
  color: rgba(107, 114, 128, 1);
}

.drop-highlight {
  position: relative;
  top: 0;
  z-index: 3;
  width: 100%;
  height: 1px;
  background: var(--purple-color);
  display: block;
}

.drop-highlight::before {
  left: 0;
}

.drop-highlight::after {
  right: 0;
}

.drop-highlight::after,
.drop-highlight::before {
  position: absolute;
	content: '';
	display: block;
	width: 4px;
	height: 4px;
	top: -2px;
	background-color: var(--purple-color);
	transform: rotate(45deg);
}