.switch-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  padding: 4px 12px;
  border-radius: 6px;
  flex-basis: 50%;
  cursor: pointer;
}

.switch-button_active {
  background-color: #FFF;
  border: 1px solid rgba(226, 227, 229, 1);
}

.switch-button__icon {
  font-size: 19px;
}

.switch-button__icon_active {
  color: var(--purple-color);
}