:root {
  --purple-color: rgba(93, 95, 239, 1);
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Inter',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  font-weight: 500;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.disabled {
  pointer-events: none;
  opacity: 50%;
}

.drop-highlight_top, .drop-highlight_bottom {
  position: relative;
  bottom: 5px;
  width: 100%;
  height: 1px;
  background: var(--purple-color);
  display: block;
}

.drop-highlight_top::before,
.drop-highlight_bottom::before {
  left: 0;
}

.drop-highlight_bottom::after,
.drop-highlight_top::after {
  right: 0;
}

.drop-highlight_bottom::after,
.drop-highlight_top::after,
.drop-highlight_top::before,
.drop-highlight_bottom::before {
  position: absolute;
	content: '';
	display: block;
	width: 4px;
	height: 4px;
	top: -2px;
	background-color: var(--purple-color);
	transform: rotate(45deg);
}

.drop-highlight_bottom {
  top: 5px;
  bottom: 0px;
}